.modal-overlay{position:fixed;top:0;left:0;right:0;bottom:0;background-color:rgba(0,0,0,.5);z-index:1000}.modal-container{position:fixed;top:0;left:0;right:0;background-color:#fff;z-index:1001;padding:20px;max-height:90vh;overflow-y:auto}.modal-header{display:flex;align-items:center;justify-content:space-between;margin-bottom:20px}.search-input-wrapper{flex:1;margin-right:20px}.search-input{width:100%;padding:12px;border:none;background-color:#f5f5f5;border-radius:8px;font-size:16px;outline:none}.search-input::-moz-placeholder{color:#999}.search-input::placeholder{color:#999}.close-button{background:#fff;border:none;padding:8px;cursor:pointer;border-radius:50%;display:flex;align-items:center;justify-content:center;color:#333;transition:background-color .2s}.close-button:hover{background-color:#f5f5f5}.section-title{font-size:16px;font-weight:600;margin-bottom:16px;color:#333}.keyword-list{display:flex;flex-wrap:wrap;gap:10px;margin-bottom:30px}.keyword-item{padding:8px 16px;background-color:#f5f5f5;border-radius:20px;color:#333;text-decoration:none;font-size:14px;transition:background-color .2s}.keyword-item:hover{background-color:#e5e5e5}.product-list{display:grid;grid-template-columns:repeat(auto-fill, minmax(200px, 1fr));gap:20px}.product-item{text-decoration:none;color:inherit}.product-image{position:relative;width:100%;padding-bottom:125%;margin-bottom:10px;overflow:hidden;border-radius:8px}.product-image img{position:absolute;top:0;left:0;width:100%;height:100%;-o-object-fit:cover;object-fit:cover}.product-info{padding:8px 0}.product-name{font-size:14px;font-weight:500;color:#333;margin:0;line-height:1.4}